import React, { useState, useEffect, useContext } from 'react';
import { AuthContext } from '../../context/AuthContext';
import CookieCard from '../CookieCard';
import { FaCookie, FaSearch } from 'react-icons/fa';

const HomeSection = () => {
  const { user } = useContext(AuthContext);
  const [cookies, setCookies] = useState([]);
  const [filteredCookies, setFilteredCookies] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const API_BASE = 'http://localhost:8080/api';

  useEffect(() => {
    const fetchCookies = async () => {
      try {
        const res = await fetch(`${API_BASE}/cookies`);
        if (res.ok) {
          const data = await res.json();
          setCookies(data);
          setFilteredCookies(data);
        }
      } catch (err) {
        console.error('Failed to fetch cookies:', err);
      } finally {
        setLoading(false);
      }
    };
    fetchCookies();
  }, []);

  useEffect(() => {
    if (searchTerm) {
      const filtered = cookies.filter(cookie =>
        cookie.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        cookie.flavor?.toLowerCase().includes(searchTerm.toLowerCase())
      );
      setFilteredCookies(filtered);
    } else {
      setFilteredCookies(cookies);
    }
  }, [searchTerm, cookies]);

  if (loading) {
    return (
      <div style={{ padding: '24px', textAlign: 'center' }}>
        <div style={{
          display: 'inline-block',
          width: '36px',
          height: '36px',
          border: '4px solid #FFB347',
          borderBottomColor: '#FF774D',
          borderRadius: '50%',
          animation: 'spin 1s linear infinite'
        }} />
        <p style={{ marginTop: '12px', color: '#374151', fontWeight: '500' }}>Loading...</p>
        <style>
          {`@keyframes spin { from {transform: rotate(0deg);} to {transform: rotate(360deg);} }`}
        </style>
      </div>
    );
  }

  return (
    <div style={{ padding: '32px', backgroundColor: '#F9FAFB', minHeight: '100vh' }}>
      {/* Header */}
      <div style={{ marginBottom: '32px' }}>
        <h1 style={{ fontSize: '28px', fontWeight: '700', color: '#111827', display: 'flex', alignItems: 'center', marginBottom: '6px' }}>
          <FaCookie style={{ marginRight: '12px', color: '#FF7F50' }} />
          Welcome, {user?.name || 'Customer'}!
        </h1>
        <p style={{ color: '#6B7280', fontSize: '16px' }}>Browse our delicious cookie collection</p>
      </div>

      {/* Search Bar */}
      <div style={{ marginBottom: '32px' }}>
        <div style={{ position: 'relative', maxWidth: '500px' }}>
          <FaSearch style={{ position: 'absolute', left: '16px', top: '50%', transform: 'translateY(-50%)', color: '#9CA3AF' }} />
          <input
            type="text"
            placeholder="Search cookies..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            style={{
              width: '100%',
              padding: '12px 16px 12px 48px',
              border: '2px solid #E5E7EB',
              borderRadius: '12px',
              fontSize: '16px',
              outline: 'none',
              transition: 'all 0.3s'
            }}
            onFocus={(e) => e.target.style.borderColor = '#FF7F50'}
            onBlur={(e) => e.target.style.borderColor = '#E5E7EB'}
          />
        </div>
      </div>

      {/* Cookie Grid */}
      {filteredCookies.length === 0 ? (
        <div style={{
          backgroundColor: '#FFFFFF',
          borderRadius: '12px',
          boxShadow: '0 8px 24px rgba(0,0,0,0.1)',
          padding: '48px',
          textAlign: 'center'
        }}>
          <p style={{ fontSize: '18px', color: '#6B7280' }}>No cookies found</p>
        </div>
      ) : (
        <div style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))',
          gap: '24px'
        }}>
          {filteredCookies.map(cookie => (
            <CookieCard
              key={cookie.id}
              cookie={cookie}
              showActions={false}
              onAddToCart={() => {}}
            />
          ))}
        </div>
      )}
    </div>
  );
};

export default HomeSection;
