import React, { useState, useEffect } from 'react';
import { FaShoppingCart, FaTrash, FaPlus, FaMinus } from 'react-icons/fa';

const CartSection = () => {
  const [cart, setCart] = useState([]);
  const [cookies, setCookies] = useState([]);
  const [loading, setLoading] = useState(true);

  const API_BASE = 'http://localhost:8080/api';

  // Load cart from localStorage
  useEffect(() => {
    const savedCart = localStorage.getItem('customerCart');
    if (savedCart) setCart(JSON.parse(savedCart));
  }, []);

  // Save cart to localStorage
  useEffect(() => {
    localStorage.setItem('customerCart', JSON.stringify(cart));
  }, [cart]);

  // Fetch cookies
  useEffect(() => {
    const fetchCookies = async () => {
      try {
        const res = await fetch(`${API_BASE}/cookies`);
        if (res.ok) {
          const data = await res.json();
          setCookies(data);
        }
      } catch (err) {
        console.error('Failed to fetch cookies:', err);
      } finally {
        setLoading(false);
      }
    };
    fetchCookies();
  }, []);

  const updateQuantity = (cookieId, newQuantity) => {
    if (newQuantity <= 0) {
      setCart(cart.filter(item => item.id !== cookieId));
    } else {
      setCart(cart.map(item =>
        item.id === cookieId ? { ...item, quantity: newQuantity } : item
      ));
    }
  };

  const removeFromCart = (cookieId) => {
    setCart(cart.filter(item => item.id !== cookieId));
  };

  const calculateTotal = () => {
    return cart.reduce((sum, item) => sum + item.price * item.quantity, 0).toFixed(2);
  };

  const handlePlaceOrder = () => {
    if (cart.length === 0) {
      alert('Your cart is empty!');
      return;
    }
    alert(`Order placed successfully!\nTotal: $${calculateTotal()}`);
    setCart([]);
  };

  if (loading) {
    return (
      <div style={{ padding: '24px', textAlign: 'center' }}>
        <div style={{
          display: 'inline-block',
          width: '36px',
          height: '36px',
          border: '4px solid #FFB347',
          borderBottomColor: '#FF774D',
          borderRadius: '50%',
          animation: 'spin 1s linear infinite'
        }} />
        <p style={{ marginTop: '12px', color: '#374151', fontWeight: '500' }}>Loading...</p>
        <style>
          {`@keyframes spin { from {transform: rotate(0deg);} to {transform: rotate(360deg);} }`}
        </style>
      </div>
    );
  }

  return (
    <div style={{ padding: '32px', backgroundColor: '#F9FAFB', minHeight: '100vh' }}>
      <div style={{ marginBottom: '32px' }}>
        <h1 style={{ fontSize: '28px', fontWeight: '700', color: '#111827', display: 'flex', alignItems: 'center', marginBottom: '6px' }}>
          <FaShoppingCart style={{ marginRight: '12px', color: '#FF7F50' }} />
          Shopping Cart
        </h1>
        <p style={{ color: '#6B7280', fontSize: '16px' }}>Review and manage your items</p>
      </div>

      {cart.length === 0 ? (
        <div style={{
          backgroundColor: '#FFFFFF',
          borderRadius: '12px',
          boxShadow: '0 8px 24px rgba(0,0,0,0.1)',
          padding: '48px',
          textAlign: 'center'
        }}>
          <FaShoppingCart style={{ fontSize: '56px', color: '#D1D5DB', marginBottom: '16px' }} />
          <h3 style={{ fontSize: '22px', fontWeight: '600', color: '#374151', marginBottom: '12px' }}>Your cart is empty</h3>
          <p style={{ color: '#6B7280', marginBottom: '32px', fontSize: '15px' }}>Add some delicious cookies to get started!</p>
          <button
            onClick={() => window.location.href = '/customer'}
            style={{
              padding: '14px 28px',
              background: 'linear-gradient(90deg, #FF7F50, #FFB347)',
              color: 'white',
              borderRadius: '10px',
              fontWeight: '600',
              cursor: 'pointer',
              border: 'none',
              boxShadow: '0 4px 12px rgba(255, 127, 80, 0.4)',
              transition: 'transform 0.2s'
            }}
            onMouseEnter={e => e.currentTarget.style.transform = 'scale(1.05)'}
            onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
          >
            Browse Cookies
          </button>
        </div>
      ) : (
        <div style={{ display: 'flex', flexWrap: 'wrap', gap: '32px' }}>
          {/* Cart Items */}
          <div style={{ flex: '2', minWidth: '320px' }}>
            <div style={{
              backgroundColor: '#FFFFFF',
              borderRadius: '12px',
              boxShadow: '0 8px 24px rgba(0,0,0,0.08)',
              overflow: 'hidden'
            }}>
              <div style={{ padding: '24px', borderBottom: '1px solid #E5E7EB' }}>
                <h2 style={{ fontSize: '20px', fontWeight: '600', color: '#111827' }}>Cart Items ({cart.length})</h2>
              </div>
              <div>
                {cart.map(item => (
                  <div key={item.id} style={{
                    padding: '20px 24px',
                    display: 'flex',
                    alignItems: 'center',
                    gap: '16px',
                    borderBottom: '1px solid #F3F4F6',
                    transition: 'background 0.2s',
                  }}>
                    <div style={{ flexShrink: 0 }}>
                      <div style={{
                        width: '64px',
                        height: '64px',
                        backgroundColor: '#FFEDD5',
                        borderRadius: '12px',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        fontSize: '28px'
                      }}>
                        üç™
                      </div>
                    </div>
                    <div style={{ flex: 1, minWidth: 0 }}>
                      <h3 style={{ fontSize: '16px', fontWeight: '600', color: '#111827' }}>{item.name}</h3>
                      <p style={{ fontSize: '14px', color: '#6B7280' }}>{item.flavor}</p>
                      <p style={{ fontSize: '16px', fontWeight: '700', color: '#FF7F50' }}>${item.price}</p>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                      <button
                        onClick={() => updateQuantity(item.id, item.quantity - 1)}
                        style={{
                          padding: '6px',
                          borderRadius: '50%',
                          backgroundColor: '#F3F4F6',
                          border: 'none',
                          cursor: 'pointer',
                          transition: 'background 0.2s'
                        }}
                        onMouseEnter={e => e.currentTarget.style.backgroundColor = '#E5E7EB'}
                        onMouseLeave={e => e.currentTarget.style.backgroundColor = '#F3F4F6'}
                      >
                        <FaMinus size={12} />
                      </button>
                      <span style={{ width: '32px', textAlign: 'center', fontWeight: '600', fontSize: '15px' }}>{item.quantity}</span>
                      <button
                        onClick={() => updateQuantity(item.id, item.quantity + 1)}
                        style={{
                          padding: '6px',
                          borderRadius: '50%',
                          backgroundColor: '#F3F4F6',
                          border: 'none',
                          cursor: 'pointer',
                          transition: 'background 0.2s'
                        }}
                        onMouseEnter={e => e.currentTarget.style.backgroundColor = '#E5E7EB'}
                        onMouseLeave={e => e.currentTarget.style.backgroundColor = '#F3F4F6'}
                      >
                        <FaPlus size={12} />
                      </button>
                    </div>
                    <button
                      onClick={() => removeFromCart(item.id)}
                      style={{
                        padding: '6px',
                        color: 'white',
                        borderRadius: '50%',
                        backgroundColor: '#EF4444',
                        border: 'none',
                        cursor: 'pointer',
                        transition: 'transform 0.2s'
                      }}
                      onMouseEnter={e => e.currentTarget.style.transform = 'scale(1.1)'}
                      onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
                    >
                      <FaTrash size={14} />
                    </button>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Order Summary */}
          <div style={{ flex: '1', minWidth: '280px' }}>
            <div style={{
              backgroundColor: '#FFFFFF',
              borderRadius: '12px',
              boxShadow: '0 8px 24px rgba(0,0,0,0.08)',
              padding: '24px',
              position: 'sticky',
              top: '24px'
            }}>
              <h3 style={{ fontSize: '20px', fontWeight: '600', color: '#111827', marginBottom: '16px' }}>Order Summary</h3>
              <div style={{ marginBottom: '24px' }}>
                {cart.map(item => (
                  <div key={item.id} style={{ display: 'flex', justifyContent: 'space-between', fontSize: '14px', marginBottom: '6px' }}>
                    <span style={{ color: '#6B7280' }}>{item.name} x {item.quantity}</span>
                    <span style={{ fontWeight: '500' }}>${(item.price * item.quantity).toFixed(2)}</span>
                  </div>
                ))}
              </div>
              <div style={{ borderTop: '1px solid #E5E7EB', paddingTop: '16px', display: 'flex', justifyContent: 'space-between', fontSize: '16px', fontWeight: 'bold' }}>
                <span>Total:</span>
                <span style={{ color: '#FF7F50' }}>${calculateTotal()}</span>
              </div>
              <button
                onClick={handlePlaceOrder}
                style={{
                  width: '100%',
                  marginTop: '24px',
                  padding: '14px',
                  background: 'linear-gradient(90deg, #FF7F50, #FFB347)',
                  color: 'white',
                  borderRadius: '10px',
                  border: 'none',
                  fontWeight: '600',
                  cursor: 'pointer',
                  boxShadow: '0 4px 16px rgba(255,127,80,0.3)',
                  transition: 'transform 0.2s'
                }}
                onMouseEnter={e => e.currentTarget.style.transform = 'scale(1.03)'}
                onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
              >
                Place Order
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default CartSection;
